<template>
  <div :style="cssVariables">
     <div class="back"></div>
    <div class="registration-form">
      <header>
        <h1>{{$t('contact.title')}}</h1>
        <p>{{$t('contact.description')}}</p>
      </header>
      <form>
        <div class="input-section email-section" :class="{'fold-up':level>0}">
          <div class="input-container">
            <div class="error-hand">{{errorMsg}}</div>
            <input class="email" type="email" v-model="emailVal" :placeholder="$t('contact.email-placeholder')"  autocomplete="off"/>
          </div>
          <div class="animated-button"><span class="icon-paper-plane" @click="errorType = 'dialog', errorMsg=$t('contact.invalid-email')" :class="{'next':emailStatus}"><i class="fas fa-at"></i></span><span class="next-button email" @click="changeLevel"  ><i class="fa fa-arrow-up"></i></span></div>
        </div>
        <div class="input-section title-section" :class="{'folded':level==0,'fold-up':level>1}">
          <div class="input-container">
            <div class="error-hand">{{errorMsg}}</div>
            <input class="title" type="text" v-model="titleVal" :placeholder="$t('contact.title-placeholder')"   />
          </div>
          <div class="animated-button"><span class="icon-lock"  :class="{'next':titleStatus}"><i class="fas fa-asterisk"></i></span><span class="next-button title"  @click="changeLevel"><i class="fa fa-arrow-up"></i></span></div>
        </div>
        <div class="input-section repeat-content-section"  :class="{'folded':level<2,'fold-up':level>2}">
          <div class="input-container">
                <div class="error-hand">{{errorMsg}}</div>
                <textarea class="repeat-title"  v-model="contentVal"  :placeholder="$t('contact.message-placeholder')"  ></textarea>
             </div>
            <div class="animated-button"><span class="icon-repeat-lock" :class="{'next':contentStatus}"><i class="fas fa-align-center"></i></span><span class="next-button repeat-title"  @click="changeLevel"><i class="fa fa-paper-plane"></i></span></div>
         
        </div>
        <div class="success" :class="{'success-ok':level>2}"> 
          <p>{{$t('contact.result')}}</p>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { defineComponent ,ref, watch, computed} from "vue";

export default defineComponent({
  setup(){
    const level = ref(0)
    const emailStatus = ref(false)
    const titleStatus = ref(false)
    const contentStatus = ref(false)
    const emailVal = ref('')
    const titleVal = ref('')
    const errorMsg = ref('')
    const errorType = ref('')
    const contentVal = ref('')
    const changeLevel = () =>{
      if((level.value == 1 && titleVal.value.length<=75) || (level.value == 2 && contentVal.value.length<=750) || ![1,2].includes(level.value)  ){
        level.value +=1
        errorMsg.value = ''
      }
      
    }

    const validateEmail= (email) =>{
      const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    }

    watch(emailVal, (value) => {
      errorMsg.value = ''
      errorType.value = ''
      if(value.length>0 && validateEmail(value)){
        emailStatus.value = true
      }else{
        emailStatus.value = false
      }
    })

    watch(titleVal, (value) => {
      if(value.length>0){
        titleStatus.value = true
      }else{
        titleStatus.value = false
      }

        errorMsg.value = 75-value.length
    })

    watch(contentVal, (value) => {
      if(value.length>0){
        contentStatus.value = true
      }else{
        contentStatus.value = false
      }

      
        errorMsg.value = 750-value.length
    })

    watch(level, (value) => {
      if(value==0){
        errorType.value = 'dialog'
      }else{
        errorType.value = 'counter'
      }
    })

     const cssVariables = computed(()=>{
      return {
        "--input-height":"75px",
        "--success-margin":level.value==3 ?   "0" : "-75px",
        "--error-position":errorType.value == 'dialog' ? "center" : errorType.value == 'counter' ? 'right' : 'left',
        "--error-color":errorType.value == 'dialog' || (errorType.value == 'counter' && ((contentVal.value.length>750) || titleVal.value.length>75) ) ? '#eb223f' :  "#343A40",
      }
    })

    return{
      level,
      changeLevel,
      emailStatus,
      titleStatus,
      contentStatus,
      emailVal,
      titleVal,
      contentVal,
      cssVariables,
      validateEmail,
      errorType,
      errorMsg

      
    }
  }

 
})
</script>

<style lang="scss" scoped>

.success-ok{
  margin-top:0;
}
.next-button:hover{
  cursor: pointer;
}
$input-height: 75px;


.back {
  position: absolute;
  width: 100%;
  height: 100%;
}

.registration-form {
  width: 400px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0%);
  top: 15%;
  
  background: transparent;
  
  header {
    position: relative;
    z-index: 4;
    background: white;
    padding: 20px 40px;
    border: 1px solid #f0f0f0;
    border-radius: 15px 15px 0 0;
    
    h1 {
      font-weight: 900;
      letter-spacing: 1.5px;
      color: #333;
      font-size: 23px;
      text-transform: uppercase;
      margin: 0;
    }
    
    p {
      word-spacing: 0px;
      color: rgb(159, 172, 182); 
      font-size: 17px;
      margin: 0;
      margin-top: 5px;
    }
  }
  
  form {
    position: relative;
  }
  
  .input-section {
    width: 100%;
    position: absolute;
    display: flex;
    left: 50%;
    transform: translate(-50%, 0);
    height: $input-height;
    border-radius: 0 0 15px 15px;
    overflow: hidden;
    z-index: 2;
    box-shadow: 0 3px 2px rgba(0,0,0,0.1);
    transition: all 0.2s ease-in;
    
    &.folded {
      width: 95%;
      margin-top: 10px;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 1;
      .input-container{
        background-color: #fff;
        width: 100%;          
      }      
      input, textarea {
        background-color: #fff;
      }
      span {
            background-color: #fff;
          }
    }
    
    &.folded + .folded {
      width: 90%;
      margin-top: 20px;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      
      .input-container{
            background-color: #fff;
          input {
            background-color: #fff;
          }
          
          

          textarea{
            background-color: #fff;
          }
      }

      span {
            background-color: #fff;
          }
      
    }
    
    &.fold-up {
      margin-top: -$input-height;
    }
  }
  .input-container{
    background: #fff;
    width: 80%;
    padding: 20px 40px;
    margin: 0;
    border: 0;
  }
  .error-hand{
    width: 100%;
    height: 20%;
    font-size: .9em;
    font-weight: 600;
    line-height: 20%;
    text-align: var(--error-position);
    color: var(--error-color);
  }
  form input, textarea {
    background: #fff;
    color: #343A40;
    width: 100%;
    height: 50%;
    padding-top: 5%;
    line-height: 50%;
    border: 0;
    
    &:focus {
      outline: none;
    }
    
    &::placeholder {
      color: #343A40;
      font-weight: 100;
    }
  }
}

.animated-button {
  width: 20%;
  height: 100%;
  background-color: rgb(107, 230, 179);
  
  span {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    line-height: $input-height;
    text-align: center;
    height: $input-height;
    transition: all 0.2s ease-in;
    
    i {
      font-size: 25px;
      color: rgb(16, 185, 115);
    }
  }
  
  .next-button {
    background: transparent;
    color: rgb(16, 185, 115);
    font-weight: 100;
    width: 100%;
    border: 0;
  }
}

.next {
  margin-top: -$input-height;
}

.success {
  width: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  left: 50%;
  transform: translate(-50%, 0);
  height: $input-height;
  border-radius: 0 0 15px 15px;
  overflow: hidden;
  z-index: 2;
  box-shadow: 0px 0px 100px rgba(0,0,0,0.2);
  transition: all 0.2s ease-in;
  background: rgb(16, 185, 115);
  margin-top: var(--success-margin);
  
  p {
    color: white;
    font-weight: 900;
    letter-spacing: 2px;
    font-size: 18px;
    width: 100%;
    text-align: center;
  }
}
</style>